import React from 'react';
import './css/headerslider.css'
import {Container,Row,Col,Jumbotron} from  'react-bootstrap'
import baa from  './assets/baa.jpg'
import water from  './assets/water.jpg'
import {IoIosArrowDropright,IoIosArrowDropleft} from 'react-icons/io'
class Headerslider extends React.Component{
constructor(props){
  super(props);
  this.state={
   xvalue:0
  }
this.nextslider=nextslider.bind(this);
this.prevslider=prevslider.bind(this);

 function nextslider(){

this.setState({xvalue:-100});

}
function prevslider(){
  	
this.setState({xvalue:0});

}
}

componentDidMount(){
}
  render(){

  return (
<Container fluid>
<Row className="headerslider">

<Col xs="12" sm="12" lg="12" className="headerslidercol" >
<div className="forpreandnextbutton">
<button className="idprevbutton" onClick={this.prevslider}>
<IoIosArrowDropleft size={25}/>
</button>

<button className="idnextbutton" onClick={this.nextslider}>
<IoIosArrowDropright size={25} />
</button>

</div>
<div className="sectionforimageslider" style={{'transform':`translateX(${this.state.xvalue}%)`}}>
<img src={baa}/>
<img src={water}/>
</div>
</Col>
</Row>
<div className="forchekinandoutholder">

<div  id="forarrive">
Arrive
<input type="date" name="arrive"/>
</div>

<div  id="fordeparture">
Departure
<input type="date" name="arrive"/>
</div>

<div id="forbookbutton"> 
<button type="button" name="button">
book now!
</button>
</div>

</div>

</Container>
  
  );
}
}

export default Headerslider;
