import React,{Component} from 'react'
import './css/nav.css'
import logo from './assets/logo.png'
import {Link,NavLink} from 'react-router-dom';
import { FaCoffee,FaIoIosMenu} from 'react-icons/fa';
import {IoIosMenu,IoMdHome} from 'react-icons/io';
class Nav extends Component{
	constructor(){
		super()
		this.state={
     togglemenuvalue:false,
     exwidth:50,
     forres:""
		}
    this.togglemenu=togglemenu.bind(this);
     function togglemenu(){
     this.setState({togglemenuvalue:true});
     this.setState({forres:"animatewidth"});

     
     }
	}

 render(){
 	return(

       <div>
       

        <section>
        <div  className={this.state.togglemenuvalue?"forresponsivemenu animatewidth":"forresponsivemenu"}>
        <div className="resforlog">
        <img src={logo} style={{width:"100px",height:"30px;",marginLeft:"33%"}}/>
        </div>
        <div className="resformenus">

        <ul>

       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/" className="link" activeClassName="linkactive"><IoMdHome size={19} style={{marginTop:"-3px"}}/>&nbsp;&nbsp;home</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/about" className="link"  activeClassName="linkactive">about</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/itinerary" className="link">itinerary</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="#" className="link">gallery</NavLink></li>
       <li onClick={()=>this.setState({togglemenuvalue:false})}><NavLink to="/travel" className="link">travel info</NavLink></li>
        </ul>
        </div>
       </div>
       <div className={this.state.togglemenuvalue?"forbackresponsive fullwidth":"forbackresponsive"} onClick={()=>this.setState({togglemenuvalue:false})}>
       <p></p>
       </div>
       </section>
        :""
           
     
    
        <div className="fornav">
        
        <div className="forlog">

        <img src={logo} style={{width:"110px",height:"40px;"}}/>
        </div>

        <div className="formenus">
        <ul>
       <li><NavLink to="/" className="link" activeClassName="linkactive"><IoMdHome size={19} style={{marginTop:"-3px"}}/>&nbsp;&nbsp;home</NavLink></li>
       <li><NavLink to="/about" className="link"  activeClassName="linkactive">about</NavLink></li>
       <li><NavLink to="/itinerary" className="link">itinerary</NavLink></li>
       <li><Link to="#" className="link">gallery</Link></li>
       <li><Link  to="/travel" className="link">travel info</Link></li>
     </ul>
        </div>
         <button type="button" className="buttontoggle" onClick={()=>this.togglemenu()}>
             <IoIosMenu size={30}/>
         </button>
      
        </div>

        </div>

 		);
 }	
}
export default Nav